.photos {
  position: relative;
  width: 100%;
  &:before {
    content: '';
    display: block;
    height: 0;
    padding-bottom: 88.3%; }
  &__pic {
    position: absolute;
    box-shadow: 0 2px 20px rgba(#B7B6B6,.5);
    opacity: 0;
    transform: translateX(-20%);
    transition: transform .4s, opacity .4s;
    &:nth-child(1) {
      top: 0;
      left: 3%;
      width: 32%;
      transition-delay: .4s;
      z-index: 1; }
    &:nth-child(2) {
      top: 21%;
      left: 26%;
      width: 66%;
      transition-delay: .2s;
      z-index: 3; }
    &:nth-child(3) {
      top: 58%;
      right: 0;
      width: 38%;
      z-index: 4; }
    &:nth-child(4) {
      left: 0;
      bottom: 0;
      width: 48%;
      transition-delay: .6s;
      z-index: 2; } }
  &_right &__pic {
    transform: translateX(20%); }
  &.visible &__pic {
    opacity: 1;
    transform: translateX(0); } }

.projects {
  &__group {
    display: flex;
    margin: 0 -10px;
    align-items: flex-start;
    @include m {
      display: block; } }
  &__col {
    flex: 0 0 45.9%;
    &:last-child {
      flex: 0 0 54.1%; } }
  &__col:last-child {
    display: flex;
    flex-wrap: wrap; }
  &__col:last-child &__item {
    &:nth-child(1) {
      flex: 0 0 calc(42.46% - 20px); }
    &:nth-child(2) {
      flex: 0 0 calc(57.54% - 20px); }
    &:nth-child(3) {
      flex: 0 0 calc(100% - 20px); } }
  &__item {
    display: flex;
    position: relative;
    margin: 0 10px 20px;
    @include bs;
    color: $cl;
    transition: box-shadow .2s, transform .2s;
    overflow: hidden; }
  &__item:hover {
    @include bs-lg;
    transform: scale(1.02); }
  &__item:hover &__foot {
    opacity: 1; }
  &__preview {
    flex: 0 0 100%; }
  &__pic {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover; }
  &__foot {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 10px 40px 10px 25px;
    background: white;
    opacity: 0;
    transform: translate3d(0,0,0);
    transition: opacity .2s;
    z-index: 2;
    .icon {
      position: absolute;
      top: 50%;
      right: 20px;
      margin-top: -5px;
      font-size: 10px;
      fill: $green;
      transform: rotate(-90deg); } }
  &__title {
    @include fs;
    color: $green; }
  &__text {
    font-size: 14px; } }

.section {
