@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content
  }
}

.carousel {
  &.carousel-slider {
    top: 0;
    left: 0;

    .carousel-item {
      width: 100%;
      height: 100%;
      min-height: 600px;
      position: absolute;
      top: 0;
      left: 0;
      
      @include r(1023) {
        min-height: 500px;
      }
      
      @include r(767) {
        min-height: 300px;
      }

      h2 {
        font-size: 24px;
        font-weight: 500;
        line-height: 32px;
      }

      p {
        font-size: 15px;
      }
    }
  }

  // overflow: hidden;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  height: 600px;
  perspective: 500px;
  transform-style: preserve-3d;
  transform-origin: 0% 50%;
  
  @include r(1023) {
    height: 500px;
  }

  @include r(767) {
    height: 300px;
  }

  .carousel-item {
    display: none;
    // visibility: hidden;
    width: 600px;
    height: 600px;
    position: absolute;
    top: 0;
    left: 0;
    
    @include r(1023) {
      width: 500px;
      height: 500px;
    }
    
    @include r(767) {
      width: 300px;
      height: 300px;
    }

    & > img {
      width: 100%;
    }
  }

  .indicators {
    position: absolute;
    text-align: center;
    left: 0;
    right: 0;
    bottom: -30px;
    margin: 0;

    .indicator-item {
      &.active {
        background-color: #2ABFBF;
      }

      display: inline-block;
      position: relative;
      cursor: pointer;
      height: 12px;
      width: 12px;
      margin: 0 12px;
      background-color: rgba(black,.1);
      transition: background-color .2s;
      border-radius: 50%;
    }
  }

  // Materialbox compatibility
  &.scrolling .carousel-item .materialboxed,
  .carousel-item:not(.active) .materialboxed {
    pointer-events: none;
  }
}
